<template>
  <ul class="swiper-pagination">
    <li class="page" @click.stop="changeIndex('minus')"></li>
    <li class="page" @click.stop="changeIndex(0)">
      <img
        src="@/assets/images/home/home-pic-1.jpg"
        alt="18度居"
        class="bg-img"
      />
      <div class="page-mask flex-center" :class="{ active: modelValue === 0 }">
        18Du Ju
      </div>
    </li>
    <li class="page" @click.stop="changeIndex(1)">
      <img
        src="@/assets/images/home/home-pic-2.jpg"
        alt="18度居"
        class="bg-img"
      />
      <div class="page-mask flex-center" :class="{ active: modelValue === 1 }">
        Easy Life
      </div>
    </li>
    <li class="page" @click.stop="changeIndex(2)">
      <img
        src="@/assets/images/home/home-pic-3.jpg"
        alt="18度居"
        class="bg-img"
      />
      <div class="page-mask flex-center" :class="{ active: modelValue === 2 }">
        Tolerance
      </div>
    </li>
    <li class="page" @click.stop="changeIndex(3)">
      <img
        src="@/assets/images/home/home-pic-4.jpg"
        alt="18度居"
        class="bg-img"
      />
      <div class="page-mask flex-center" :class="{ active: modelValue === 3 }">
        All-Encompassing
      </div>
    </li>
    <li class="page" @click.stop="changeIndex('plus')"></li>
  </ul>
</template>

<script lang="ts">
import { defineComponent } from "vue";
export default defineComponent({
  name: 'PageCom',
  model: {
    props: 'modelValue',
    event: 'update:modelValue'
  },
  props: {
    modelValue: {
      type: Number,
      default: 0
    }
  },
  setup (props, {emit}) {
    const changeIndex: (type: string | number) => void = (type: string | number): void => {
      let index: number = props.modelValue
      if (type === 'plus') {
        index++
        if (index >= 3) {
          index = 3
        }
      } else if (type === 'minus') {
        index--
        if (index <= 0) {
          index = 0
        }
      } else {
        if (typeof type === 'number') {
          index = type
        }
      }
      emit('update:modelValue', index)
    }
    return {
      changeIndex
    }
  }
})
</script>

<style lang="scss">
.swiper-pagination {
  position: fixed;
  left: 0;
  bottom: 0;
  display: flex;
  width: 100%;
  .page {
    position: relative;
    width: 16.65%;
    height: 120px;
    cursor: pointer;
    &:last-child,
    &:first-child {
      background: rgba(72, 72, 72, 0.85) url("../assets/images/icon-arrow.png")
        center center no-repeat;
      background-size: 26px auto;
    }
    &:last-child {
      transform: rotate(180deg);
    }
    .bg-img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    .page-mask {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 120px;
      font-weight: 600;
      font-size: 14px;
      line-height: 20px;
      color: #fff;
      background-color: rgba(72, 72, 72, 0.8);
      transition: all 0.2s ease-in;
      &.active {
        height: 40px;
        transition: all 0.2s ease-in;
      }
    }
  }
}
</style>