<script lang="ts">
import { defineComponent, onMounted, reactive, toRefs, watchEffect } from "vue";
import "swiper/dist/css/swiper.css";
import Swiper from "swiper";

interface tsData {
  slideIndex: number
}

export default defineComponent({
  name: "Home",
  components: {},
  setup() {
    const data: tsData  = reactive({
      slideIndex: 0
    })
    const refData = toRefs(data)
    let swiper: any = null
    const changeIndex = () => {
      if (data.slideIndex >=3) {
        data.slideIndex = 3
      } else {
        data.slideIndex++
      }
    }

    onMounted(() => {
      swiper = new Swiper(".swiper-container", {
        direction: "vertical",
        mousewheel: true,
        forceToAxis: true,
        keyboard: {
          enabled: true,
          onlyInViewport: false,
        },
        initialSlide: data.slideIndex,
        observer: true, // 修改swiper自己或子元素时，自动初始化swiper
        observeParents: true, // 修改swiper的父元素时，自动初始化swiper
        on: {
          slideChangeTransitionEnd: function () {
            data['slideIndex'] = swiper['realIndex']
          }
        }
      });
    });
    watchEffect(() => {
      if (swiper || data.slideIndex) {
        swiper.slideTo(data.slideIndex, 1000, false)
      }
    })
    return {
      ...refData,
      changeIndex
    };
  },
});
</script>


