      </div>
    </div>
    <PageCom v-model="slideIndex" />
    <div class="tip-down" @click.stop="changeIndex">
      <p class="txt">认识她</p>
      <img src="@/assets/images/icon-down-tip.png" alt="18度居" class="icon-down">
    </div>
  </div>
